<template>
  <div>
    <div class="container containerp">
      <section class="sales-data flex-sa">
        <router-link :to="{ path: '/clientRanking' }">
          <div class="title">
            <img src="@assets/images/a1.png" />
          </div>
          <span>门店排名</span>
        </router-link>
        <router-link :to="{ path: '/performanceRanking' }">
          <div class="title">
            <img src="@assets/images/a2.png" />
          </div>
          <span>业绩排名</span>
        </router-link>

        <router-link :to="{ path: '/customerNumberRanking' }">
          <div class="title">
            <img src="@assets/images/a4.png" />
          </div>
          <span>推广数排名</span>
        </router-link>
      </section>
      <div class="line-charts">
        <LineCharts style="height:200px" :chart-data="LineChartsData" :options="options"></LineCharts>
      </div>
      <section class="customer-analysis" v-for="(item,indexs) in data" :key="indexs">
        <div class="list">
          <router-link :to="{ path: '/getMonthData/'+item.month }" class="item">
            <div class="center">
              <p>
                <span class="month">{{item.month}}月</span> 销售金额(元)
              </p>
              <div>{{toThousands(item.total_price)}}</div>
            </div>
            <div class="item-bottom">
              <div>
                消费客户
                <span>{{item.cust_num}}</span> 个
              </div>
              <div>
                订单数量
                <span>{{item.order_num}}</span> 个
              </div>
            </div>
          </router-link>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import { AchievementData } from "@api/public";
import { Icon, Popup } from "vant";
import Loadings from "@components/Loadings";
import LineCharts from "@components/LineCharts";
export default {
  name: "Analysis",
  components: {
    [Icon.name]: Icon,
    Loadings,
    LineCharts,
    [Popup.name]: Popup,
  },
  props: {},
  data: function () {
    return {
      data: null,
      data_date_list: [],
      data_count_list: [],
    };
  },
  created() {
    this.data = [
      { cust_num: 0, month: "09", order_num: 0, total_price: 0 },
      { cust_num: 0, month: "08", order_num: 0, total_price: 8520 },
      { cust_num: 0, month: "07", order_num: 0, total_price: 54250 },
      { cust_num: 0, month: "06", order_num: 0, total_price: 45320 },
      { cust_num: 0, month: "05", order_num: 0, total_price: 69530 },
      { cust_num: 0, month: "04", order_num: 0, total_price: 75220 },
      { cust_num: 0, month: "03", order_num: 0, total_price: 54440 },
      { cust_num: 0, month: "02", order_num: 0, total_price: 45250 },
      { cust_num: 0, month: "01", order_num: 0, total_price: 82280 },
    ];

    var data_date_list = [];
    var data_count_list = [];
    this.data.forEach((item) => {
      data_date_list.unshift(item.month + "月");
      data_count_list.unshift(item.total_price);
    });
    
    this.data_date_list = data_date_list;
    this.data_count_list = data_count_list;
    this._initChar();
  },
  mounted: function () {},
  methods: {
    _initChar() {
      (this.options = {
        responsive: true,
        maintainAspectRatio: false,
        legend: false,
      }),
        (this.LineChartsData = {
          labels: this.data_date_list,
          datasets: [
            {
              label: "",
              borderColor: "#092e48",
              backgroundColor: "rgba(72,207,173,0)",
              data: this.data_count_list,
            },
          ],
        });
    },
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
  },
};
</script>
<style scoped>
.line-charts {
  background: #fff;
  padding: 20px 0;
}
.chartjs-render-monitor {
  height: 250px !important;
}
.top {
  height: 210px;
  background: #092e48;
  text-align: center;
  color: #fff;
  padding-top: 10px;
  font-size: 12px;
}

.sales-data {
  background: #092e48;
  height: 110px;
  text-align: center;
  color: #fff;
}

.sales-data > a {
  color: #fff;
}

.sales-data > a .title img {
  width: 35px;
}

.sales-data .caption {
  color: #fff;

  padding-left: 19px;
  margin-bottom: 10px;
  font-size: 16px;
}

.sales-data span,
.to-do .flex-sa span {
  font-size: 12px;
}

.to-do,
.query,
.customer-analysis {
  box-shadow: 0 0 5px #eee;
  background-color: #fff;
  margin: 10px;
  border-radius: 5px;
}

.to-do .caption,
.query .caption,
.customer-analysis .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #666;
}

.to-do .flex-sa {
  text-align: center;
  padding: 30px 0;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 18px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}

.customer-analysis {
  margin-top: 25px;
}

.customer-analysis .list {
  line-height: 28px;
  font-size: 14px;
}

.customer-analysis .list .item .title {
  text-align: center;
  border-bottom: 1px solid #eee;
  padding: 5px 10px;

  line-height: 40px;
  font-size: 18px;
}

.customer-analysis .list .item {
  padding-left: 8px;
  text-align: center;
  color: #666;
  display: block;
  padding-bottom: 15px;
  display: block;
}

.customer-analysis .list .item img {
  width: 15px;
  vertical-align: sub;
}

.customer-analysis .list .item .title div {
  font-size: 12px;
  padding-right: 5px;
}

.value {
  color: #000;
  font-size: 22px;
}

.value span {
  font-size: 12px;
  color: #999;
}

.sub {
  font-size: 12px;
  line-height: 20px;
  color: #999;
}

.item {
  text-align: center;
  background: #fff;
  color: #fff;
}

.item-bottom {
  overflow: hidden;
}

.item-bottom div {
  float: left;
  color: #8c8d91;
  font-size: 14px;
  width: 50%;
  text-align: center;
}

.item-bottom div:first-child {
  border-right: 1px dashed #8c8d91;
}

.item-bottom div span {
  color: #000;
}

.item .center {
  padding: 20px 0 20px;
}

.item .center div {
  font-size: 26px;
  font-weight: bold;
  color: #d2ac5b;
}
.month {
  color: #d2ac5b;
  font-size: 16px;
  font-weight: bold;
}

.item .center p {
  color: #8c8d91;
  margin-bottom: 10px;
}
</style>
